@charset "UTF-8";

// =======================================
// Font Settings
// =======================================

// 본문(paragraph) 사이즈
$body-fz: 16px;

// 본문(paragraph) 기본 컬러
$base-color: #222;

// 기본 행간 비율
$line-height: 1.2;

// 본문 행간 비율
$base-line: $body-fz * 1.2;

// 적용 웹 폰트
$font-family-ko: 'Roboto', 'Noto Sans KR', sans-serif;
$font-family-en: 'Roboto', 'Noto Sans KR', Arial, sans-serif, serif;

$roboto: 'Roboto', sans-serif;

//color
$c-border: #DDD;
$c-bg: #ECEEF3;

$c-gray: #666;
$c-gray2: #999;
$c-gray3: #BBB;
$c-gray4: #DDD;
$c-gray5: #EEE;

$c-black: #222;
$c-white: #FFF;

$c-blue: #346AFF;
$c-red: #F0142F;
$c-yellow: #FFE645;

$c-primary: #222;